    <riddle v-for="riddle in riddles.slice(0,progress+1)" :key="riddle.id" v-bind="riddle" @clear="clear($event)" @open_hint="open_hint($event)" class="riddle"></riddle>
  </div>
</template>

<script>
import Riddle from './components/Riddle.vue'
import HintModal from './components/HintModal.vue'

export default {
  name: 'App',
  data: function(){
    return {
