          clues: [
            {id: 0,img_url: '/calendar.jpg'},
            {id: 1,img_url: '/memo.jpg'},
            {id: 2,img_url: '/first_riddles/1-Fri.jpg',hint: "イラストを漢字に直して比較してみましょう"},
            {id: 3,img_url: '/first_riddles/1-Sat.jpg',hint: "6/26,27に関連するものをたどってみましょう"},
            {id: 4,img_url: '/first_riddles/1-Wed.jpg',hint: "矢印方向に熟語ができるようにしましょう"},
            {id: 5,img_url: '/first_riddles/1-Mon.jpg',hint: "単語の始まりと終わりに注目しましょう"},
            {id: 6,img_url: '/first_riddles/1-Sun.jpg',hint: "二字熟語が6つできるようにしましょう"},
            {id: 7,img_url: '/first_riddles/1-Thu.jpg',hint: "図形の欠けた部分に注目しましょう"},
            {id: 8,img_url: '/first_riddles/1-Tue.jpg',hint: "漢字の一部に注目しましょう"}
          ],
          hint: "初めの週を見て、それをカレンダーから探し出しましょう",
          answers: ["642","６４２","ろくよんに","ロクヨンニ"]
        },
        {
          id: 1,
          clues: [
            {id: 0,img_url: '/menu.jpg'},
            {id: 1,img_url: '/skeleton.jpg'}
          ],
          hint: "文字数の多い語から埋めてみましょう",
          answers: ["消しゴム","消ゴム","けしごむ","ケシゴム","けしゴム"]
        },
        {
          id: 2,
          clues: [
            {id: 0,img_url: '/last.jpg'}
          ],
          hint: "漢字の一部に注意してみましょう\n今まで参照したものにも気を配りましょう",
          answers: ["ドッキリ大成功","どっきり大成功","ドッキリだいせいこう","どっきりだいせいこう","ドッキリダイセイコウ"]
        }
      ],
      last_riddle_id: 2,
      progress: 0,
      isHintShowed: false,
      hint_content: "no_hint"
    }
  },
  components: {Riddle,HintModal},
  methods: {
    clear: function(id){
      if(id == this.last_riddle_id){
        this.complete()
      }else{
        this.progress += 1
      }
    },
    complete: function(){
      alert("complete")
    },
    open_hint: function(content){
      this.hint_content = content
      this.isHintShowed = true
    },
    close_hint: function(){
      this.isHintShowed = false
    }
  }
}
</script>

